<template>
  <div id="app">
    <el-container>
      <el-header style="min-height: 80px; height: 5vh;">
        <h1 style="text-align:center">minium 测试报告</h1>
      </el-header>

      <el-container  style="height: 80vh">
        <el-aside style="min-width: 400px; background-color: rgb(238, 241, 246)" v-if="taskMenuShow">            
          <el-menu              
            style="min-height: 80vh"
            :default-active="defaultOpeneds[1]"
            :default-openeds="defaultOpeneds"
            :unique-opened="true"
            @select="handleSelect"
            @open="handleOpen"
            @close="handleClose">
            <el-submenu v-for="(taskData, index) in taskDatas" :index="index+''">
              <template slot="title">
                <span>{{taskData.summary.start_time}}</span>
              </template>
              <el-menu-item v-for="(case_data, case_idx) in taskData.case_datas" :index="index+'-'+case_idx">
              
              <el-row :gutter="10" type="flex" justify="space-between">
                <el-col :span="18" style="word-wrap:break-word;word-break:break-all;overflow: auto;">{{case_data.case_name}}</el-col>
                <el-col :span="4">
                  <el-tag :type="statusClass(case_data)" effect="dark">{{ statusMsg(case_data) }}</el-tag>
                </el-col>
              </el-row>
              </el-menu-item>
            </el-submenu>              
          </el-menu>
        </el-aside>

        <el-container direction="vertical">
          <el-main>
          <el-container>
            <el-main>
                <Summary :summary="summary"/>
            </el-main>
          </el-container>
          <el-container>
            <el-aside width="500px" v-if="!taskMenuShow">
              <el-col>
                <el-row style="padding-top:2em">
                  <el-table
                    ref="singleTable"
                    :data="caseDatas"
                    style="width: 100%"
                    highlight-current-row
                    v-on:row-click="tableRowClick"
                  >
                    <el-table-column prop="run_time" label="执行时间" width="180"></el-table-column>
                    <el-table-column prop="case_name" label="用例名称" width="180"></el-table-column>
                    <el-table-column prop="case_name" label="状态" width="100">
                      <template slot-scope="scope">
                        <el-tag :type="statusClass(scope.row)" effect="dark">{{ statusMsg(scope.row) }}</el-tag>
                      </template>
                    </el-table-column>
                  </el-table>
                </el-row>
              </el-col>
            </el-aside>
            <el-main>
              <CaseDetail v-if="caseDetail" :caseDetail="caseDetail"/>
            </el-main>
          </el-container>

          </el-main>
        </el-container>
      </el-container>
      <el-footer style="text-align:center; height: 15vh; font-size: 12px; color: #aaa">
        <p>Minium Designed by 微信测试中心</p>
        <p>Copyright &copy; 1998-2019 Tencent Inc. All Rights Reserved</p>
      </el-footer>
    </el-container>
    <el-backtop>
      <div
      style="{
        height: 100%;
        width: 100%;
        background-color: #f2f5f6;
        box-shadow: 0 0 6px rgba(0,0,0, .12);
        text-align: center;
        line-height: 40px;
        color: #1989fa;
      }"
    >
      Top
    </div>
    </el-backtop>
  </div>
</template>

<script>
import CaseDetail from './components/CaseDetail'
import Summary from './components/Summary'
const axios = require('axios')

export default {
  name: 'app',
  components: { CaseDetail, Summary },
  data () {
    return {
      metadata: null,
      caseDetail: 0,
      taskDetail: null,
      taskMenuShow: false,
      defaultOpeneds: ['0', '0-0']
    }
  },
  mounted () {
    axios.get(`${process.env.BASE_URL}meta.json`).then(resp => {
      // 处理截图预览信息
      this.metadata = resp.data
      function processPreviewSrcList(case_data) {
        let screen_info = case_data.screen_info
        let urls = []
        for (let _screen_info of screen_info) {
          urls.push(case_data.relative + "/" + _screen_info.path)
        }
        case_data.preview_src_list = urls
        for (let i = 0; i < screen_info.length; i++) {
          screen_info[i].preview_src_list = urls.slice(i).concat( urls.slice(0, i) )
        }
      }
      switch (this.metadata.version || 1) {
        case 1:
          this.taskMenuShow = false
          for (let case_data of this.metadata.case_datas) {
            processPreviewSrcList(case_data)
          }
          this.$refs.singleTable.setCurrentRow(this.caseDetail = this.metadata.case_datas[0])
          break;
        case 2:
          if (this.metadata.task_datas.length < 2) this.taskMenuShow = false
          else this.taskMenuShow = true
          for (let task_data of this.metadata.task_datas) {
            for (let case_data of task_data.case_datas) {
              processPreviewSrcList(case_data)
            }
          }
          this.taskDetail = this.metadata.task_datas[0]
          this.caseDetail = this.taskDetail && this.taskDetail.case_datas[0]
          break;
      }
      
    })
  },
  methods: {
    handleOpen(key, keyPath) {
      // 重置summary和casedetail数据
      console.log("@open",key, keyPath);
      this.taskDetail = this.taskDatas[key]
      this.caseDetail = this.taskDetail.case_datas[0]
      this.defaultOpeneds = [key, key + '-0']
    },
    handleClose(key, keyPath) {
      console.log("@close",key, keyPath);
    },
    handleSelect(key, keyPath) {
      let [index, case_idx] = key.split("-")
      console.log("@select", key, keyPath, index, case_idx)
      this.taskDetail = this.taskDatas[index]
      this.caseDetail = this.taskDetail.case_datas[case_idx]
    },
    tableRowClassName ({ row, index }) {
      if (row.success) {
        return 'success-row'
      } else if (row.failure) {
        return 'warning-row'
      }
      return 'error-row'
    },
    tableRowClick (detail) {
      console.log(detail)
      this.caseDetail = detail
    },
    statusClass (caseDetail) {
      if (caseDetail.success) {
        return 'success'
      }
      if (caseDetail.is_error) {
        return 'danger'
      }
      if (caseDetail.is_failure) {
        return 'warning'
      }
    },
    statusMsg (caseDetail) {
      if (caseDetail.success) {
        return '成功'
      }
      if (caseDetail.is_error) {
        return '异常'
      }
      if (caseDetail.is_failure) {
        return '失败'
      }
    }
  },
  computed: {
    taskDatas() {
      if (this.metadata) {
        return this.metadata.task_datas
      } else {
        return []
      }
    },
    caseDatas () {
      if (this.metadata) {
        return this.metadata.version === 1 ? this.metadata.case_datas : (this.metadata.task_datas[0] && this.metadata.task_datas[0].case_datas)
      } else {
        return []
      }
    },
    summary () {
      if (this.metadata) {
        if (this.metadata.version === 1) return this.metadata.summary
        else if(this.taskDetail) return this.taskDetail.summary
        else if (this.metadata.task_datas[0]) return this.metadata.task_datas[0].summary
        else return {}
      }
      return {}
    }
  }
}
</script>

<style>
.el-table .warning-row {
  background: #e6a23c;
}

.el-table .success-row {
  background: #67c23a;
}

.el-table .error-row {
  background: #f56c6c;
}
</style>
