          <template slot-scope="scope">
            <el-progress :text-inside="true" :stroke-width="22" :percentage="getPercentage(scope.row)" :status="getPercentage(scope.row) > 90 ? 'success': (getPercentage(scope.row) > 60 ? 'warning': 'exception')"></el-progress>
          </template>
        </el-table-column>
        <el-table-column prop="start_time" label="开始时间"></el-table-column>
        <el-table-column prop="end_time" label="结束时间"></el-table-column>
        <el-table-column prop="costs" label="耗时"></el-table-column>
      </el-table>
    </el-row>

    <el-row style="height: 20px">
    </el-row>

    <!-- 分包总览 -->
    <el-row>
      <el-table v-if="summary.detail&&summary.detail.length>1" :data="summary.detail" style="width: 100%" stripe border>
        <el-table-column prop="name" label="套件"></el-table-column>
        <el-table-column prop="case_num" label="case数"></el-table-column>
        <el-table-column prop="success" label="通过"></el-table-column>
        <el-table-column prop="failed" label="失败"></el-table-column>
        <el-table-column prop="error" label="case异常"></el-table-column>
