          <template slot-scope="scope">
            <el-progress :text-inside="true" :stroke-width="22" :percentage="getPercentage(scope.row)" :status="getPercentage(scope.row) > 90 ? 'success': (getPercentage(scope.row) > 60 ? 'warning': 'exception')">
            </el-progress>
          </template>
        </el-table-column>
        <el-table-column prop="start_time" label="开始时间"></el-table-column>
        <el-table-column prop="end_time" label="结束时间"></el-table-column>
        <el-table-column prop="costs" label="耗时"></el-table-column>
      </el-table>
    </el-row>
  </el-main>
</el-container>
<!-- 总览 -->
</template>

<script>
export default {
  name: 'Summary',
  props: {
    summary: Object,
  },
  data() {
    return {}
  },
  methods: {
    getPercentage({
      success,
      case_num
    }) {
      return parseInt(success * 100 / case_num)
    }
  }
}
</script>
